:root {
  --title-color: rgba(0,0,0,0.85);
  --primary-text-color: rgba(0,0,0,0.65);
  --shadow-color: rgba(255,255,255,0.25);
  --shadow-color: rgba(0,0,0,15);
  --bkg-color: #fff;
  --icon-color: var(--primary-text-color);
  --icon-hover-color: var(--title-color);
  --primary-text-color: rgba(0,0,0,0.65);
  --secondary-text-color: rgba(0,0,0,0.45);


  --tiitle-size: 64px;
  --primary-text-size: 36px;
  --secondary-text-size: 32px;
  --thirdary-text-size: 24px;
  --forthary-text-size: 20px;
  --fifthary-text-size: 16px;

  --filter: url(#surface);
  --filter-opacity: 1;
}

:root[data-theme=dark] {
  --title-color: rgba(255,255,255,1);
  --primary-text-color: rgba(255,255,255,0.85);
  --secondary-text-color: rgba(255,255,255,0.65);
  --shadow-color: rgba(255,255,255,0.25);
  --bkg-color: #121212;
  --filter: none;
  --filter-opacity: 0;
}