.home {
  min-height: 832px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 30vw;
  a {
    text-decoration: none;
  }
  .content {
    .title{
      font-size: var(--tiitle-size);
      color: var(--title-color);
    }
    .sub_title{
      font-size: var(--thirdary-text-size);
      color: var(--thirdary-text-color);
      margin: 28px 0 38px;
      p{
        &:nth-child(1){
          margin-bottom: 4px;
        }
      }
    }
    .socials {
      i {
        font-size: 32px;
        margin-right: 8px;
      }
    }
  }
  .portrait{
    padding: 18px;
    position: relative;
    line-height: 0;
    margin-right: 12px;
    img{
      width: 16vw;
      max-width: 300px;
      box-shadow: 0 0 10px 6px #80541b, 0 0 4px 16px #62411b, 0 0 12px 24px #76531f, 0 0 0 30px #251b09;
      border-radius: 1px;
      font-size: 0;
    }
    &::after{
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-image: repeating-linear-gradient(-45deg, rgba(123,88,11, 0.2), rgb(128 92 13) 3px, rgba(123,88,11, 0.2) 5px, transparent 6px, transparent 10px) 14;
      border-width: 6px;
      border-style: solid;
      border-radius: 2px;
      filter: brightness(110%);
    }
  }
  .hello{
    display: inline-block;
    animation: handShakeLong 10s linear infinite;
    &:hover{
      animation: handShakeShort 2s linear infinite;
    }
  }
}

@keyframes handShakeLong {
  0% {
    transform: rotate(0deg);
  }

  40% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(-9deg);
  }

  60% {
    transform: rotate(9deg);
  }

  70% {
    transform: rotate(-4deg);
  }

  80% {
    transform: rotate(4deg);
  }

  100% {
    transform: rotate(0deg);
  }
}


@keyframes handShakeShort {
  0% {
    transform: rotate(0deg);
  }

  60% {
    transform: rotate(0deg);
  }

  65% {
    transform: rotate(-9deg);
  }

  75% {
    transform: rotate(9deg);
  }

  80% {
    transform: rotate(-4deg);
  }

  85% {
    transform: rotate(4deg);
  }

  100% {
    transform: rotate(0deg);
  }
}