@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.header {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: var(--bkg-color);
  box-shadow: none;
  transition: box-shadow 0.28s ease;
  overflow: hidden;
  &.scroll {
    box-shadow: 0 -2px 10px var(--shadow-color);
  }
  &::after{
    content:'';
    filter: var(--filter);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    right: 0;
    opacity: var(--filter-opacity);
    transition: opacity 0.8s ease-in-out;
  }
  .content {
    max-width: 1280px;
    height: 68px;
    padding: 0 108px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    .nav{
      display: flex;
      align-items: center;
    }
    .mode {
      font-size: var(--thirdary-text-size);
      cursor: pointer;
      color: #000;
      user-select: none;
    }
    nav {
      margin-right: 84px;
      display: flex;
    }
    .menu {
      margin-right: 62px;
      overflow: hidden;
      cursor: pointer;
      font-size: var(--forthary-text-size);
      position: relative;
      a{
        color: var(--secondary-text-color);
        text-decoration: none;
        display: inline-block;
        position: relative;
        padding: 8px 16px;
        transition: color .08s ease-in-out;
        &.active {
          color: var(--title-color);
        }
      }
      svg {
        position: absolute;
      }
      &:last-child{
        margin-right: 0;
      }
      .line{
        @include coverer;
        stroke: var(--primary-text-color);
        stroke-width: 2px;
        fill: transparent;
        stroke-dasharray: 0 400;
        @include transition(0.8s);
      }
      &:hover{
        .line{
          stroke-dasharray: 400 0;
        }
      }
    }
  }
}