@import '../../styles/variables.scss';
.skill {
  .position{
    margin-top: 80px;
    span {
      padding: 0 20px;
      color: var(--secondary-text-color);
      font-size: var(--thirdary-text-size);
      transition: all .28s ease-in-out;
      &.active{
        color: var(--title-color);
        font-weight: 600;
      }
    }
  }
  .panel{
    width: 100%;
    text-align: center;
    margin-top: 140px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    color: var(--primary-text-color);
    .item{
      display: flex;
      flex-direction: column;
    }
    li {
      margin: 16px 0;
      text-align: left;
      &:before {
            content:"• ";
        }
    }
    &_wrapper {
      width: 200%;
      height: 200px;
      display: flex;
      flex-wrap: nowrap;
      font-size: var(--fifthdary-text-size);
      i{
        font-size: 36px;
      }
      p{
        margin: 16px 0;
        font-size: var(--forthary-text-size);
        font-weight: 600;
      }
    }
  }
}