.experience{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100% ;
  flex-direction: row;
  margin-bottom: 160px;
  &:nth-child(2n) {
    flex-direction: row-reverse;
  }
  img {
    object-fit: contain;
    height: 480px;
    width: 480px;;
  }
  p{
    color: var(--title-color);
    font-size: var(--forthary-text-size);
  }
  .company{
    font-size: var(--secondary-text-size);
    margin-bottom: 32px;
  }
  .role {
    font-size: var(--fifthary-text-size);
    color: var(--title-color);
    margin-left: 16px;
  }
  ul {
    font-size: var(--fifthary-text-size);
    color: var(--primary-text-color);
    margin-bottom: 20px;
    &:last-child{
      margin-bottom: 0px;
    }
  }
  li {
    margin: 8px 0;
    text-align: left;
    &:before {
          content:"• ";
      }
  }
}