.App {
  main{
    max-width: 1280px;
    margin: auto;
    .main {
      padding: 0 68px;
      overflow: hidden;
    }
  }
  .section{
    min-height: 832px;
    padding-top: 160px;
    padding-bottom: 20px;
    overflow: hidden;
    .title {
      text-align: center;
      color: var(--title-color);
      font-size: var(--secondary-text-size);
      margin-bottom: 18px;
    }
    .desc {
      text-align: center;
      color: var(--secondary-text-color);
      font-size: var(--forthary-text-size);
      margin-bottom: 60px;
    }
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
